workbox_routing_registerRoute(({ url }) => {
              const isApiServer = /^[^.]+\.[^.]+\.nomercy\.tv/.test(url.hostname);
              const isImageRequest = url.pathname.match(/\.(jpg|jpeg|png|gif|webp|avif)$/i);
              return isApiServer && isImageRequest;
            }, new workbox_strategies_CacheFirst({ "cacheName":"cover-images", plugins: [new workbox_expiration_ExpirationPlugin({ maxEntries: 100000 }), new workbox_cacheable_response_CacheableResponsePlugin({ statuses: [ 0, 200 ] })] }), 'GET');
